.filter {

    &__list{
        border-bottom: 1px solid #aaa;
        margin: 0 0 10px;
        padding: 0;

        &--item {
            display: inline-block;
            border: 1px solid transparent;
            border-bottom: none;
            bottom: -1px;
            position: relative;
            list-style: none;
            padding: 6px 12px;
            cursor: pointer;

            &--selected {
                background: #fff;
                border-color: #aaa;
                border-radius: 5px 5px 0 0;
            }
            
            &:focus {
                //box-shadow: 0 0 5px hsl(208, 99%, 50%);
                //border-color: hsl(208, 99%, 50%);
                outline: none;

                &:after {
                    content: "";
                    position: absolute;
                    height: 5px;
                    left: -4px;
                    right: -4px;
                    bottom: -5px;
                    background: #fff;
                }
            }
        }
    }
}